	methods: {
		skipTo (data, tag = '') {
			const { value } = data || {};
			if (tag === 'after-sales') {
				uni.navigateTo({
				    url: `/pages/mine/after-sales`
				});
			} else if(tag === 'all-orders') {
				uni.navigateTo({
				    url: "/pages/mine/all-orders"
				});
			} else {
				uni.navigateTo({
				    url: `/pages/mine/${value}`
				});
			}
		}
	}
